import { useParams } from "react-router";

export default function UserEdit() {
  const { id } = useParams();
  console.log(id);
  return (
    <div className="userEdit">
      <label for="firstname">First Name</label>
      <input type="text" className="form-control mb-3" id="firstname"></input>

      <label for="lastname">Last Name</label>
      <input type="text" className="form-control mb-3" id="lastname"></input>

      <label for="email">Email</label>
      <input type="email" className="form-control mb-3" id="email"></input>
      <div className="m-auto">
        <button className="btn btn-primary mr-3">Save</button>
        <button className="btn btn-primary">Cancel</button>
      </div>
    </div>
  );
}
