import { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import { Link } from "react-router-dom";

export default function ProdEdit(props) {
  const { id } = useParams();

  let [prodName, setprodName] = useState("");
  let [price, setprice] = useState("");
  let [stocks, setstocks] = useState("");
  let [sold, setsold] = useState("");

  let history = useHistory();
  useEffect(async () => {
    let res = await fetch(
      `https://6073d312066e7e0017e78582.mockapi.io/Products/${id}`
    );
    let product = await res.json();

    setprodName(product.name);
    setprice(product.price);
    setstocks(product.stocks);
    setsold(product.sold);
  }, []);

  let handleDelete = async (e) => {
    e.preventDefault();
    let res = await fetch(
      `https://6073d312066e7e0017e78582.mockapi.io/Products/${id}`,
      {
        method: "DELETE",
        body: JSON.stringify(),
        headers: {
          "Content-type": "application/json",
        },
      }
    );
    let products = await res.json();
    history.push("/prodlist");
  };

  let handleEdit = async (e) => {
    e.preventDefault();
    let data = {
      name: prodName,
      price,
      stocks,
      sold,
    };

    let res = await fetch(
      `https://6073d312066e7e0017e78582.mockapi.io/Products/${id}`,
      {
        method: "PUT",
        body: JSON.stringify(data),
        headers: {
          "Content-type": "application/json",
        },
      }
    );
    let products = await res.json();
    history.push("/prodlist");
  };

  return (
    <div className="container prodCreate">
      {prodName !== "" ? (
        <form>
          <div className="row">
            <div className="col-lg-6">
              <label for="prodname">Name</label>
              <input
                type="text"
                className="form-control mb-3"
                id="prodname"
                value={prodName}
                onChange={(e) => setprodName(e.target.value)}
                required
              ></input>
            </div>
            <div className="col-lg-6">
              <label for="price">Price (in Rs.)</label>
              <input
                type="number"
                className="form-control mb-3"
                id="price"
                value={price}
                onChange={(e) => setprice(e.target.value)}
                required
              ></input>
            </div>
            <div className="col-lg-6">
              <label for="stocks">Stocks in Hand</label>
              <input
                type="number"
                className="form-control mb-3"
                id="stocks"
                value={stocks}
                onChange={(e) => setstocks(e.target.value)}
                required
              ></input>
            </div>
            <div className="col-lg-6">
              <label for="sold">Sold Quantity</label>
              <input
                type="number"
                className="form-control mb-3"
                id="sold"
                value={sold}
                onChange={(e) => setsold(e.target.value)}
                required
              ></input>
            </div>
            <div className="col-lg-6">
              <button
                className="btn btn-primary mr-3"
                type="submit"
                onClick={handleEdit}
              >
                Save
              </button>
              <Link to="/prodlist">
                <button className="btn btn-primary">Cancel</button>
              </Link>
              <button className="btn btn-primary" onClick={handleDelete}>
                Delete
              </button>
            </div>
          </div>
        </form>
      ) : (
        <h1>Loading..</h1>
      )}
    </div>
  );
}
