import "./App.css";
import Sidebar from "./components/Sidebar";
import Navbar from "./components/Navbar";
import Dashboard from "./components/Dashboard";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import UserList from "./components/Userlist";
import UserCreate from "./components/Usercreate";
import UserEdit from "./components/UserEdit";
import ProdList from "./components/prodList";
import ProdCreate from "./components/prodCreate";
import ProdEdit from "./components/prodEdit";
import { UserProvider } from "./components/userContext";
import SignUpForm from "./components/Signupform";

function App() {
  return (
    <Router>
      <div id="wrapper">
        <Sidebar />
        <UserProvider>
          <div id="content-wrapper" className="d-flex flex-column">
            <div id="content">
              <Navbar />
              <Switch>
                <Route exact path="/" component={SignUpForm} />
                <Route exact path="/dashboard" component={Dashboard} />
                <Route exact path="/userlist" component={UserList} />
                <Route path="/usercreate" component={UserCreate} exact={true} />
                <Route path="/useredit/:id" component={UserEdit} exact={true} />
                <Route path="/prodlist" component={ProdList} exact={true} />
                <Route path="/prodcreate" component={ProdCreate} exact={true} />
                <Route path="/prodedit/:id" component={ProdEdit} exact={true} />
              </Switch>
            </div>
          </div>
        </UserProvider>
      </div>
    </Router>
  );
}

export default App;
