import { ErrorMessage, Form, Formik } from "formik";
import { useContext, useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import * as Yup from "yup";

import UserContext from "./userContext";

export default function ProdCreate() {
  let data = useContext(UserContext);

  let history = useHistory();

  let [prodName, setprodName] = useState("");
  let [price, setprice] = useState("");
  let [stocks, setstocks] = useState("");
  let [sold, setsold] = useState("");

  let handleSubmit = async (e) => {
    e.preventDefault();
  };

  const validate = Yup.object({
    prodName: Yup.string()
      .min(3, "Cannot be less than 3 chars")
      .max(20, "Should be less than/equal to 20 chars")
      .required("Required"),
    price: Yup.number().required("Required"),
    stocks: Yup.number().required("Required"),
    sold: Yup.number().required("Required"),
  });

  return (
    <div className="container prodCreate">
      <Formik
        initialValues={{
          prodName: "",
          price: "",
          stocks: "",
          sold: "",
        }}
        validationSchema={validate}
        onSubmit={async (values) => {
          console.log(JSON.stringify(values));
          let data = {
            name: values.prodName,
            price: values.price,
            stocks: values.stocks,
            sold: values.sold,
          };

          let res = await fetch(
            "https://6073d312066e7e0017e78582.mockapi.io/Products",
            {
              method: "POST",
              body: JSON.stringify(data),
              headers: {
                "Content-type": "application/json",
              },
            }
          );
          let products = await res.json();
          // console.log(products);

          history.push("/prodlist");
        }}
      >
        {(formik) => {
          // console.log(formik.values);
          return (
            <Form onSubmit={formik.handleSubmit}>
              <div className="row">
                <div className="col-lg-6">
                  <label htmlFor="prodName">Name</label>
                  <input
                    type="text"
                    className="form-control"
                    name="prodName"
                    value={formik.values.prodName}
                    onChange={formik.handleChange}
                  ></input>
                  <ErrorMessage
                    name="prodName"
                    component="div"
                    className="error"
                  />
                </div>
                <div className="col-lg-6">
                  <label for="price">Price (in Rs.)</label>
                  <input
                    type="number"
                    className="form-control"
                    id="price"
                    name="price"
                    onChange={formik.handleChange}
                    value={formik.values.price}
                  ></input>
                  <ErrorMessage
                    name="price"
                    component="div"
                    className="error"
                  />
                </div>
                <div className="col-lg-6">
                  <label for="stocks" className="mt-3">
                    Stocks in Hand
                  </label>
                  <input
                    type="number"
                    className="form-control"
                    id="stocks"
                    name="stocks"
                    onChange={formik.handleChange}
                    value={formik.values.stocks}
                  ></input>
                  <ErrorMessage
                    name="stocks"
                    component="div"
                    className="error"
                  />
                </div>
                <div className="col-lg-6">
                  <label for="sold" className="mt-3">
                    Sold Quantity
                  </label>
                  <input
                    type="number"
                    className="form-control"
                    id="sold"
                    name="sold"
                    onChange={formik.handleChange}
                    value={formik.values.sold}
                  ></input>
                  <ErrorMessage name="sold" component="div" className="error" />
                </div>
                <div className="col-lg-6">
                  {/* <Link to="/prodlist"> */}
                  <button type="submit" className="btn btn-primary mt-4">
                    Submit
                  </button>
                  {/* </Link> */}
                </div>
              </div>
            </Form>
          );
        }}
      </Formik>
    </div>
  );
}
