import ProjectsProgress from "./ProjectsProgress";

export default function Projects() {
  let projects = [
    {
      name: "Server Migration",
      completed: "20",
      color: "danger",
    },
    {
      name: "Sales Tracking",
      completed: "40",
      color: "warning",
    },
    {
      name: "Customer Database",
      completed: "60",
      color: "",
    },
    {
      name: "Payout Details",
      completed: "80",
      color: "info",
    },
    {
      name: "Account Setup",
      completed: "100",
      color: "success",
    },
  ];

  let colors = [
    {
      color: "Primary",
      value: "#4e73df",
    },
    {
      color: "Success",
      value: "#1cc88a",
    },
    {
      color: "Info",
      value: "#36b9cc",
    },
    {
      color: "Warning",
      value: "#f6c23e",
    },
    {
      color: "Danger",
      value: "#e74a3b",
    },
    {
      color: "Secondary",
      value: "#858796",
    },
    {
      color: "Light",
      value: "#f8f9fc",
    },
    {
      color: "Dark",
      value: "#5a5c69",
    },
  ];

  return (
    <>
      <div class="col-lg-6 mb-4">
        <div class="card shadow mb-4">
          <div class="card-header py-3">
            <h6 class="m-0 font-weight-bold text-primary">Projects</h6>
          </div>
          <div class="card-body">
            {projects.map((project) => {
              return <ProjectsProgress project={project} />;
            })}
          </div>
        </div>
        <div class="row">
          {colors.map((color) => {
            return (
              <div class="col-lg-6 mb-4">
                <div
                  class={`card bg-${color.color.toLowerCase()} text-${
                    color.color === "Light" ? "black" : "white"
                  } shadow`}
                >
                  <div class="card-body">
                    {color.color}
                    <div
                      class={`text-${
                        color.color === "Light" ? "black" : "white"
                      }-50 small`}
                    >
                      {color.value}
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
}
