export default function ProjectsProgress(props) {
  return (
    <>
      <h4 class="small font-weight-bold">
        {props.project.name}
        <span class="float-right">{props.project.completed}%</span>
      </h4>
      <div class="progress mb-4">
        <div
          class={`progress-bar bg-${props.project.color}`}
          role="progressbar"
          style={{ width: `${props.project.completed}%` }}
          aria-valuenow={`${props.project.completed}%`}
          aria-valuemin="0"
          aria-valuemax="100"
        ></div>
      </div>
    </>
  );
}
