import { ErrorMessage, Field, Form, Formik } from "formik";
import { useState } from "react";
import RocketImg from "../images/rocket.png";
import * as Yup from "yup";

function SignUpForm() {
  const validate = Yup.object({
    firstName: Yup.string()
      .min(5, "Cannot be less than 5")
      .max(20, "Cannot be more than 20 chars")
      .required("Required"),
    lastName: Yup.string()
      .min(5, "Cannot be less than 5")
      .max(20, "Cannot be more than 20 chars")
      .required("Email Required"),
    email: Yup.string().email("Email is invalid").required("Required"),
    password: Yup.string()
      .min(6, "Password must not be less than 6 characters")
      .required("Password is Required"),
    confirmPassword: Yup.string()
      .oneOf([Yup.ref("password")], "Confirm Password must match with Password")
      .required("Confirm Password Required"),
  });

  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col-lg-5">
            <h1>Signup</h1>
            <Formik
              initialValues={{
                firstName: "",
                lastName: "",
                email: "",
                password: "",
                confirmPassword: "",
              }}
              validationSchema={validate}
              onSubmit={(values) => {
                console.log(values);
                alert(JSON.stringify(values, null, 2));
              }}
            >
              {(formik) => {
                // console.log(formik.values);
                return (
                  <Form onSubmit={formik.handleSubmit}>
                    <div>
                      <label htmlFor="firstName">First Name</label>
                      <input
                        name="firstName"
                        className={`form-control `}
                        value={formik.values.firstName}
                        onChange={formik.handleChange}
                        autoComplete="off"
                        type="text"
                      />
                      <ErrorMessage
                        component="div"
                        className="error"
                        name="firstName"
                      />
                    </div>

                    <div>
                      <label htmlFor="lastName" className="mt-4">
                        Last Name
                      </label>
                      <input
                        name="lastName"
                        className="form-control "
                        value={formik.values.lastName}
                        onChange={formik.handleChange}
                        autoComplete="off"
                        type="text"
                      />
                      <ErrorMessage
                        component="div"
                        className="error"
                        name="lastName"
                      />
                    </div>
                    <div>
                      <label htmlFor="email" className="mt-4">
                        Email
                      </label>
                      <input
                        name="email"
                        className="form-control "
                        autoComplete="off"
                        value={formik.values.email}
                        onChange={formik.handleChange}
                        type="email"
                      />
                      <ErrorMessage
                        component="div"
                        className="error"
                        name="email"
                      />
                    </div>
                    <div>
                      <label htmlFor="password" className="mt-4">
                        Password
                      </label>
                      <input
                        name="password"
                        className="form-control "
                        autoComplete="off"
                        type="password"
                        value={formik.values.password}
                        onChange={formik.handleChange}
                      />
                      <ErrorMessage
                        component="div"
                        className="error"
                        name="password"
                      />
                    </div>
                    <div>
                      <label htmlFor="confirmPassword" className="mt-4">
                        Confirm Password
                      </label>
                      <input
                        name="confirmPassword"
                        className="form-control "
                        autoComplete="off"
                        type="password"
                        value={formik.values.confirmPassword}
                        onChange={formik.handleChange}
                      />
                      <ErrorMessage
                        component="div"
                        className="error"
                        name="confirmPassword"
                      />
                    </div>
                    <div className="mt-5">
                      <button className="btn btn-dark mr-3" type="submit">
                        Register
                      </button>

                      <button className="btn btn-danger" type="reset">
                        Reset
                      </button>
                    </div>
                  </Form>
                );
              }}
            </Formik>
          </div>
          <div className="col-lg-7">
            <img src={RocketImg} className="img-fluid w-100" alt="No Image" />
          </div>
        </div>
      </div>
    </>
  );
}

export default SignUpForm;
