import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import UserContext from "./userContext";

export default function ProdList() {
  let data = useContext(UserContext);
  let [prodList, useprodList] = useState([]);

  useEffect(async () => {
    let res = await fetch(
      "https://6073d312066e7e0017e78582.mockapi.io/Products"
    );
    let products = await res.json();
    console.log(products);
    useprodList([...products]);
  }, []);

  return (
    <div className="prodList">
      <Link to="/prodcreate" style={{ color: "whitesmoke" }}>
        <button className="btn btn-primary">Add Product</button>
      </Link>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">Name</th>
            <th scope="col">Price (in Rs.)</th>
            <th scope="col">Stocks </th>
            <th scope="col">Sold </th>
            <th scope="col">Action</th>
          </tr>
        </thead>
        <tbody>
          {prodList.length > 0 ? (
            prodList.map((product) => {
              return (
                <tr>
                  {/* <th scope="row">{product.id}</th> */}
                  <td>{product.name}</td>
                  <td>{product.price}</td>
                  <td>{product.stocks}</td>
                  <td>{product.sold}</td>
                  <td>
                    <Link to={`/prodedit/${product.id}`}>Edit</Link>
                  </td>
                </tr>
              );
            })
          ) : (
            <h1>Loading...</h1>
          )}
        </tbody>
      </table>
    </div>
  );
}
